.nav {
	position: fixed;
    left: 0;
    right: 0;
	z-index: 1000000;
}
@media (max-width:991px) {
	.nav {
		background-color: var(--color-pr);
		box-shadow: 1px 1px 8px 1px #ddd;
	}
}
.nav .container .logo a {
	padding: 5px 10px;
	display: block;
    font-size: 20px;
    text-transform: capitalize;
    color: var(--text-color);
    font-weight: 600;
	position: relative;
}
@media (max-width: 992px) {
	.nav .container .logo a {
		font-size: 19px;
	}
}
.nav .container .logo a::before,
.nav .container .logo a::after {
	content: '';
	position: absolute;
	width: 20px;
	height: 20px;
	transition: var(--main-transition);
}
.nav .container .logo a::before {
	bottom: 0;
	left: 0;
	border-bottom: 3px solid var(--main-color);
	border-left: 3px solid var(--main-color);
}
.nav .container .logo a::after {
	top: 0;
	right: 0;
	border-top: 3px solid var(--main-color);
	border-right: 3px solid var(--main-color);
}
.nav .container .logo a:hover::before,
.nav .container .logo a:hover::after {
	width: 100%;
}
.nav .container .logo a .first-letter {
	color: var(--main-color);
	font-family: 'Clicker Script', cursive;
}

.dropdown-toggle::after {
    display: inline-block;
    margin-left: 0.255em;
    vertical-align: 0.255em;
    content: "";
    border-top: 0;
    border-right: 0;
    border-bottom: 1.5px solid #000;
    border-left: 1.5px solid #000;
    width: 7px;
    height: 7px;
    transform: rotate(-45deg);
}